// src/Calculator.js

import React, { useState } from 'react';
import './Calculadora.css';
import 'bootstrap/dist/css/bootstrap.min.css';

const Calculadora = () => {
  const [input, setInput] = useState('');
  const [result, setResult] = useState('');
  const [theme, setTheme] = useState('light');

  const handleClick = (value) => {
    setInput(input + value);
  };

  const handleClear = () => {
    setInput('');
    setResult('');
  };

  const handleCalculate = () => {
    try {
      const evaluatedResult = eval(input); // eval puede ser peligroso, usa una alternativa ms segura en produccin
      setResult(evaluatedResult);
    } catch (error) {
      setResult('Error');
    }
  };

  const toggleTheme = () => {
    setTheme((prevTheme) => (prevTheme === 'light' ? 'dark' : 'light'));
  };

  return (
    <div className={`container d-flex justify-content-center align-items-center vh-100 ${theme}`}>
      <div className="calculator card p-3">
        <div className="text-center mb-4">
          <h4>Ingrese un valor</h4>
        </div>
        <div className="display mb-3 d-flex justify-content-between">
          
          <input type="text" value={input} readOnly className="form-control text-right" />
          <div className="result text-right">{result}</div>
          <button className="btn btn-danger" onClick={handleClear}>C</button>
        </div>
        <div className="buttons row">
          {['9', '8', '7', '/', '6', '5', '4', '*', '3', '2', '1', '+', '.', '0', '=', '-'].map((value, index) => (
            <button
              key={index}
              className={`btn col-3 ${value === '/' || value === '*' || value === '+' || value === '-' || value === '=' ? 'btn-primary' : 'btn-secondary'} mb-2`}
              onClick={value === '=' ? handleCalculate : () => handleClick(value)}
            >
              {value}
            </button>
          ))}
        </div>
      </div>
      <button className="btn btn-outline-secondary position-absolute bottom-0 start-0 m-3" onClick={toggleTheme}>
        {theme === 'light' ? 'Dark Theme' : 'Light Theme'}
      </button>
    </div>
  );
};

export default Calculadora;
